import { ElectronService } from "../../ElectronService";
import { MenuCommand } from "../../decorators/MenuCommand";
import { OnMenuClick } from "../../decorators/OnMenuClick";
import { IBaseMenuCommand } from "./IBaseMenuCommand";

@MenuCommand("edit", "edit-deletee", "삭제하기", ["ctrl", "d"])
export class EditDeleteCommand implements IBaseMenuCommand {
    @OnMenuClick("edit-delete")
    action(ev: any): void {
        ElectronService.getInstance().showErrorMessageBox(
            "알림",
            "삭제 기능을 실행하였습니다."
        );
    }
}
